body {
  background-color: rgb(10,10,10);
  min-height: 100vh;
}

h1 {
  font-size: calc(60px + 2vmin);
  font-weight: bold;
  font-family: 'Caveat', cursive;
}

.left {
  display: flex;
  margin: auto;
  justify-content: space-between;
} 

#alec {
  max-width: 300px;
}

#lec {
  margin-top: -2%;
}

#maya {
  position: fixed;
  bottom: 0;
  right: 0;
}

.center {
  color: red;
  text-align: center;
  min-width: 100%;
}

@media (prefers-reduced-motion: no-preference) {
   .num-spin {
    animation: num-spin infinite 2s linear;
  }
}

@keyframes num-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#num {
  color: black;
  text-shadow:  -6px -2px red, 6px 2px #FFFFFF ;
  margin: auto;
  width: 10%;
  font-family: 'Roboto Slab', serif;
}

#name {
  color: black;
  text-shadow:  -6px -2px red, 6px 2px #FFFFFF ;
  margin-left: 2%;
  min-width: 100%;
  position: fixed;
  z-index:-1;
  bottom: 0;
  line-height: 0;
  font-family: 'Roboto Slab', serif;
}
